
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#container {
  position: absolute;
  width: 100%;
  height: 100%;
  display:flex;
  flex-direction:column;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.login {
    flex:1 1;
    background-color: #ffffff;
    display: flex;
    position: relative;
    flex-direction: column;
    padding: 50px 20px 40px 20px;
    justify-content: center;
    align-items: center;
}

.settings-btn {
    position:absolute;
    background-color: transparent;
    border: 0;
    top: 20px;
    right: 20px;
    color: #777777;
}

.settings-btn:hover {
    cursor: pointer;
}

button:focus {
    outline: 0;
}

.image-dummy {
    background-color: skyblue;
    width: 160px;
    height: 160px;
    margin: 50px 0;
}

.profile-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 0.5px solid #e7e7e7;
}

.profile-item {
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.profile-image {
    width:50px;
    height:50px;
    border-radius:50%;
    margin: 10px 15px;
}

.profile-nickname {
    font-weight: bold;
    font-size: 12pt;
}

.settings-btn:hover {
    cursor: pointer;
}
.settings {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex: 1 1;
}

.settings-header {
    height: 70px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    border-bottom: 0.5px solid #e7e7e7;
}

.back-button {
    margin: 0px 15px;
}

.back-button:hover {
    cursor: pointer;
}

.settings-header-title {
    font-weight: bold;
}

.settings-list {
    flex: 1 1;
    background-color: #f7f7f7;
    display: flex;
    flex-direction: column;
}

.settings-list > .settings-list-item {
    height: 50px;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}

.settings-list > .settings-list-item > .settings-list-item-title {
    margin: 0px 15px;
}

.settings-footer {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    height: 45px;
    border-top: 0.5px solid #e7e7e7;
}

.logout-button {
    margin: 0px 15px;
}

.logout-button:hover {
    cursor: pointer;
}
